.header {
    // position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;

    h1 {
        margin: 0;
        padding: .5rem;

        @include mini-phone {
            font-size: 1.7rem;
        }

        @include phone {
            font-size: 2.4rem;
        }


        @include desktop {
            font-size: 3.4rem;
        }
    }

    .contador {
        margin-left: 5rem;
        font-size: 3.4rem;
        background-color: $azulClaro;
        padding: 1rem 2rem;

        @include phone {
            margin-left: 0;
            font-size: 3rem;
        }

        p {
            margin: 0;
            font-weight: bold;
        }

        @include mini-phone {
            margin-left: 0;
            font-size: 1.9rem;
            padding: .5rem 1rem;
        }
    }

}