@mixin mini-phone {
    @media (max-width: 300px) {
        @content;
    }
}

@mixin phone {
    @media screen and (min-width: 300px) and (max-width: 599px) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: 600px) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: 1200px) {
        @content;
    }
}