.table-container {
    height: 80%;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 5px;
        background: transparent;
        opacity: 0.5;
    }

    &::-webkit-scrollbar-thumb {
        // background: $naranja;
        background: rgba($color: $grisClaro, $alpha: 0.1);
        border-radius: 2px;
    }
    p{
        font-size: 2.6rem;
        @include mini-phone{
            font-size: 2rem;
        }
    }
}

.table {
    width: 100%;
    height: auto;

    thead {
        position: sticky;
        background-color: #282c34;
        top: -1%;

        th {
            padding: .5rem 0;
        }
    }

    tbody tr {
        white-space: pre-line !important;
        font-size: 1.5rem;
        padding: .5rem 0;

        @include phone {
            font-size: 1.9rem;
        }

        @include tablet {
            font-size: 2.6rem;
            letter-spacing: 1px;

        }

        @include desktop {
            font-size: 3.2rem;

        }

        td {
            padding: .8rem 0;
        }
    }

    .impar {
        background-color: $azulClaro;
    }
}